import Language from "./Language";

const { ccclass, property } = cc._decorator;

@ccclass
export default class TUT extends cc.Component {

    @property(cc.SpriteFrame)
    img: cc.SpriteFrame[] = [];

    @property(cc.Node)
    playbtn: cc.Node = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        this.onLanguageChange()

        cc.systemEvent.on('LANG_CHAN', this.onLanguageChange, this);
    }

    onLanguageChange() {
        this.node.children.map(el => el.active = false)
        let txtTut = Language.getInstance().get('TUT').split('$img');

        txtTut.map((txt, id) => {
            this.node.addChild(this.getTextNode(txt));
            this.node.addChild(this.getSpriteNode(this.img[id]));
        })

        if (this.playbtn) {
            let newNode = cc.instantiate(this.playbtn);
            newNode.active = true
            this.node.addChild(newNode);
            // this.node.addChild(this.playbtn);
            // this.playbtn.active = true;
        }

        this.node.children[0].active = false
        this.node.children[1].active = false

    }

    getTextNode(txt: string) {
        let newNode = cc.instantiate(this.node.children[0]);
        newNode.getComponent(cc.RichText).string = txt;
        newNode.active = true
        return newNode;
    }

    getSpriteNode(img: cc.SpriteFrame) {
        let newNode = cc.instantiate(this.node.children[1]);
        newNode.getComponent(cc.Sprite).spriteFrame = img;
        newNode.active = true
        return newNode;
    }

    // update (dt) {}
}
