    public static prepareLBglobal(listGlobal = []) {
        return listGlobal.map(user => {
            if (user?.user_id == Api.playerId) {
                return { ...user, score: +Api.coin }
            }
            return user
        })
    }

    public static renderLeaderBoard(res: object, typeOfLeaderBoard: string, leaderboard: Leaderboard) {
        let players = res[typeOfLeaderBoard]?.filter(e => e.user_id) || [];
        const lengthOfLeaderBoad = players.length;
        if (!lengthOfLeaderBoad) return;
        const lastIndex = lengthOfLeaderBoad - 1;
        players.map((e, i) => {
            leaderboard.render(
                i,
                e.username,
                e.score,
                e.photo,
                e.user_id,
                typeOfLeaderBoard
            );
            if (i === lastIndex) leaderboard.onLoadComplete();
        });
    }

    public static preloadRewardedVideo(callback: () => void = null) {
        if (!window.hasOwnProperty('FBInstant') || Api.preloadedRewardedVideo || !Config.reward_video) {
            if (callback) callback();
            return;
        }

        FBInstant.getRewardedVideoAsync(Config.reward_video).then(function (rewarded) {
            // Load the Ad asynchronously
            Api.preloadedRewardedVideo = rewarded;
            return Api.preloadedRewardedVideo.loadAsync();
        }).then(() => {
            cc.log('Rewarded video preloaded');
            if (callback) callback();
        }).catch((e) => {
            Api.preloadedRewardedVideo = null;
            console.error(e.message);
            if (callback) callback();
        });
    }

