    private static setScoreAsync(score) {
        if (!window.hasOwnProperty('FBInstant') && !Api.yandexPlayer) {
            return customPromise
        }

        if (Api.yandexPlayer) {
            try {
                return Api.yandexLB.setLeaderboardScore(Config.YandexLBName, score) || customPromise
            } catch (error) {
                console.log('====================================');
                console.log('save coin err', error);
                console.log('====================================');
            }
        }

        const data = { score, userId: Api.playerId, name: Api.username, photo: Api.photo }
        return fetch(`${Api.lb_api}/${Api.lb_id}`,
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify(data)
            }
        )
    }

    public static randomBonus() {
        const r = Math.random();
        if (r < 0.05) return 400000;
        if (r < 0.2) return 300000;
        if (r < 0.5) return 200000;
        return 100000;
    }
