const { ccclass, property } = cc._decorator;
import Media from "./Media";
import Language from "./Language";
@ccclass
export default class ConfirmViewAd extends cc.Component {

    @property(cc.Node)
    popup: cc.Node = null;

    @property(cc.Label)
    header: cc.Label = null;

    @property(cc.Label)
    content: cc.Label = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        this.showNoAd = this.showNoAd.bind(this);
        this.hide = this.hide.bind(this);
        cc.systemEvent.on('noAd', this.showNoAd)
        // cc.systemEvent.once('noAd', ({ title, content }) => {
        //     alert(title+ content)
        // })
    }

    showNoAd() {
        this.popup.active = true;
        this.header.string = Language.getInstance().get('notice')
        // this.content.string = ''
    }

    hide() {
        Media.playAudio('click')
        this.popup.active = false
    }

    // update (dt) {}
}
