import Popup from "../../Popup";
import Config from "../../Config";
import DailyBonusItem from "./DailyBonusItem";
import Api from "../../Api";
import AutoHide from "../../AutoHide";
import util from "../../util";
import EventKeys from "../../EventKeys";
import Language from "../../Language";
import Media from "../../Media";
import UI from "../../UI";

const { ccclass, property } = cc._decorator;

@ccclass
export default class DailyBonus extends cc.Component {
    @property(Popup)
    popup: Popup = null;
    @property(AutoHide)
    toast: AutoHide = null;
    @property(DailyBonusItem)
    days: DailyBonusItem[] = [];
    @property(cc.Node)
    effect: cc.Node = null;
    current: number = 0;
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}


    // update (dt) {}

    show(day: number, claimed: boolean = false) {
        day = +day
        if (claimed) day--;
        if (!day) day = 7;
        if (day > Config.dailyBonus.length) {
            day = 1;
        }
        this.current = day;
        this.popup.open(this.node, 2);
        for (let i = 0; i < this.days.length; i++) {
            const d = i + 1;
            const claimable = (d == day) && !claimed;
            if (d < day) {
                this.days[i].setClaimable(false, true);
            } else if (d == day) {
                this.days[i].setClaimable(claimable, claimed);
            } else {
                this.days[i].setClaimable(true, false);
            }
        }
    }

    onClaim(sender: cc.Node, day: string) {
        Media.playAudio('click');
        if (Api.dailyBonusClaimed || +day != Api.dailyBonusDay) return this.showMsgClaimed();
        const date = day ? parseInt(day) : this.current;
        this.popup.close(this.node, 2);
        this.onClaimCompl(sender, date, false);
        cc.systemEvent.emit('claim_daily_bonus', date, Config.dailyBonus[date - 1], false);

    }

    showMsgClaimed() {
        const msg = Language.getInstance().get('backTomorrow')
        this.toast.openWithText(null, msg);
    }

    onClaimExtra(sender: cc.Node, day: string) {
        Media.playAudio('click');
        if (Api.dailyBonusClaimed || +day != Api.dailyBonusDay) return this.showMsgClaimed();
        const date = day ? parseInt(day) : this.current;
        Api.logEvent(EventKeys.POPUP_DAILY_REWARD_AD);
        UI.contentViewAd = 'dailyConfirm'
        Api.showRewardedVideo(() => {
            this.popup.close(this.node, 2);
            this.onClaimCompl(sender, date, true);
            cc.systemEvent.emit('claim_daily_bonus', date, Config.dailyBonus[date - 1], true);

        }, () => {
            cc.systemEvent.emit('claim_daily_bonus_fail', date, Config.dailyBonus[date - 1], true);
            Api.logEvent(EventKeys.POPUP_DAILY_REWARD_AD_ERROR);
            this.popup.close(this.node, 2);
        });
    }

    onClaimCompl(sender, day: number, double: boolean) {
        // const gift = Config.dailyBonus[day - 1];
        // if (!gift.coin) {
        //     return
        // }
        // const e = cc.instantiate(this.effect);
        // e.active = true;
        // e.position = sender.target.position;
        // e.scale = 1;
        // e.parent = cc.director.getScene().getChildByName("Canvas");
        // const coinBonus = double ? gift.coin * 2 : gift.coin;
        // e.getComponent(cc.Label).string = util.numberFormat(coinBonus);
        // e.runAction(cc.sequence(
        //     cc.moveBy(1.2, 0, 250),
        //     cc.removeSelf()
        // ))

        const gift = Config.dailyBonus[day - 1];
        let msg = ''

        if (gift.coin) {
            const coinBonus = double ? gift.coin * 2 : gift.coin;
            // msg = `You've got ` + util.numberFormat(coinBonus);
            msg = Language.getInstance().get('addCoin').replace(/\$value/, util.numberFormat(coinBonus))
        } else if (gift.ticket) {
            let ticket = gift.ticket * (double ? 2 : 1)
            msg = Language.getInstance().get('addSpin').replace(/\$value/, ticket.toString())
        } else return

        setTimeout(() => {
            this.popup.open(null, 8, 200, { type: gift.coin ? 'coin' : 'slotmachine', text: msg })
        }, 300);
        // const e = cc.instantiate(this.effect);
        // e.active = true;
        // e.position = sender.target.position;
        // e.scale = 1;
        // e.parent = cc.director.getScene().getChildByName("Canvas");
        // var urlFont = "Fonts/SVN-The Voice Heavy Italic.ttf";
        // e.getComponent(cc.Label).font = cc.url.raw(urlFont)
        // e.getComponent(cc.Label).lineHeight = 80
        // // e.getComponent(cc.Label).node.color = cc.color(230, 148, 46, 255)
        // e.getComponent(cc.Label).string = msg;
        // e.runAction(cc.sequence(
        //     cc.moveBy(1.2, 0, 250),
        //     cc.removeSelf()
        // ))
    }
}
