// Learn Attribute:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/reference/attributes.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/reference/attributes.html
// Learn life-cycle callbacks:
//  - [Chinese] https://docs.cocos.com/creator/manual/zh/scripting/life-cycle-callbacks.html
//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html
enum actionType {
    double, insurr, stand, hit, split, push, bust, "black jack"
}
type actionTypeString = keyof typeof actionType;
const { ccclass, property } = cc._decorator;

@ccclass
export default class Player extends cc.Component {
    // @property(cc.Node)
    // fold: cc.Node = null;
    @property(Timer)
    timer: Timer = null;
    @property(cc.Label)
    coin: cc.Label = null;
    @property(cc.Label)
    username: cc.Label = null;
    @property(cc.Node)
    marker: cc.Node = null;
    @property(cc.Sprite)
    avatar: cc.Sprite = null;
    @property(TweenMove)
    effect: TweenMove = null;
    @property(cc.Label)
    pointLbl: cc.Label = null;
    @property(sp.Skeleton)
    winEffect: sp.Skeleton = null;
    @property(sp.Skeleton)
    loseEffect: sp.Skeleton = null;

    seat: number = 0;
    cards: Array<Card> = [];
    prepareCards: CardGroup = new CardGroup([], CardGroup.Ungrouped);
    inRound: boolean = true;
    coinVal: number = 1;
    cardMargin: number = 0;
    cardRootPosition: cc.Vec2 = null;
    cardDirection: number = null;
    labelCardCounter: cc.Label = null;
    numOfAce: number = 0
    point: number = 0

    bonusType: number = 0 // 0 nothing,  1 double, 2 insurr
    isSplit: number = 0

    subUser: Player = null
    isSubUser: boolean = false;
    timeout = null;
    @property(cc.Sprite)
    userActionEffect: cc.Sprite = null

    @property(cc.Label)
    betLbl: cc.Label = null;

    isBonusAce = false;

    @property(cc.Sprite)
    blackjack: cc.Sprite = null

    prevAction = '';
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        // this.fold.active = false;
        this.timer.hide();
        this.marker.active = false;
        // this.cardMargin = this.seat == 0 ? Player.CARD_MARGIN :
        //     0.5 * Player.CARD_MARGIN;
        this.cardMargin = 0.5 * Player.CARD_MARGIN;
        this.cardDirection = this.seat == 1 ? -1 : 1;
        this.labelCardCounter = this.marker.getComponentInChildren(cc.Label);
    }

    setBonusType = (bonusType: number) => {

        try {
            let self = this
            let txtBonusUser = 'Canvas/user/bonus'
            if (!this.inRound) {
                self = this.subUser
                txtBonusUser = 'Canvas/subUser/bonus'
            }
            self.bonusType = bonusType

            let bonusLbl = cc.find(txtBonusUser);
            bonusLbl.active = !!bonusType
            // let str = bonusType == 1 ? 'x2' : '/2'
            // bonusLbl.getComponent(cc.Label).string = str
        } catch (error) {

        }
    }

    showHideActionUser(needShow = false, actionType: actionTypeString) {

        let target = this;
        if (!this.inRound && this.subUser) target = this.subUser;

        if (actionType == 'black jack') {
            this.blackjack.node.active = true;
            this.setInRound(false);
        } else {
            try {
                this.prevAction != 'push' && (target.userActionEffect.node.active = needShow);
                if (needShow) {
                    target.userActionEffect.getComponentInChildren(cc.Label).string = actionType.toUpperCase();
                    this.prevAction = actionType
                }
                // needShow && ()//util.assetBtn.getSpriteFrame(actionType))
            } catch (error) {

            }
        }

        if (this.timeout) {
            clearTimeout(this.timeout);
            this.timeout = null;
        }


        // this.node.runAction(cc.sequence(
        //     cc.delayTime(700)
        // ))
        needShow && setTimeout(() => {
            target.updatePoint();
        }, 700);
        // target.userActionEffect.node.scaleX = .5
        // target.userActionEffect.node.scaleY = .5
    }

    blinkShowHideActionUser(actionType: actionTypeString) {
        this.showHideActionUser(true, actionType);
        this.timeout = setTimeout(this.showHideActionUser.bind(this), Config.timeBlinkActionUser);
    }

    // update (dt) {}

    show() {
        this.node.active = true;
    }

    showAllCard = () => {
        this.cards.map(card => {
            card?.show()
        })
    }

    hide() {
        this.node.active = false;
    }

    isBot(): boolean {
        return this.seat !== Config.totalPlayer / 2;
        return this.seat > 0;
    }

    isUser(): boolean {
        return this.isSubUser || this.seat === Config.totalPlayer / 2;
    }

    isCai(): boolean {
        return this.seat === 0;
        return this.seat === Config.totalPlayer / 2;
    }

    setInRound(inRound: boolean) {
        // this.fold.active = !inRound;
        this.inRound = inRound;
    }

    isInRound() {
        return this.inRound;
    }

    changeCoin = (coin: number) => {
        let color = cc.color(0xf9, 0xd2, 0x1e, 255), addString = '+';
        if (!this.isCai()) {
            if (coin < 0) {
                color = cc.color(255, 0, 0, 255), addString = '-';
                !this.isSplit && (this.loseEffect.node.active = true)
            } else {
                !this.isSplit && (this.winEffect.node.active = true)
            }
        }

        this.effect.getComponent(cc.Label).string = addString + util.numberFormat(Math.abs(coin));
        this.effect.node.color = color
        this.effect.play();
        this.setCoin(this.coinVal + coin);
    }

    addCoin(val: number) {
        this.effect.getComponent(cc.Label).string = '+' + util.numberFormat(val);
        this.effect.node.color = cc.color(0xf9, 0xd2, 0x1e, 255);
        this.effect.play();
        this.setCoin(this.coinVal + val);
    }

    subCoin(val: number) {
        this.effect.getComponent(cc.Label).string = '-' + util.numberFormat(val);
        this.effect.node.color = cc.color(255, 0, 0, 255);
        this.effect.play();
        this.setCoin(this.coinVal - val);
    }

    setCoin(val: number) {
        this.coinVal = val;
        this.coin.string = util.numberFormat(val);
    }

    getCoin(): number {
        return this.coinVal;
    }

    setUsername(val: string) {
        if (this.isBot() && val?.length > 12) {
            val = val.substr(0, 9) + '...'
        }
        this.username.string = val;
    }

    setAvatar(sprite: cc.SpriteFrame) {
        this.avatar.spriteFrame = sprite;
    }

    setRemoteAvatar(photo) {
        cc.assetManager.loadRemote<cc.Texture2D>(photo, (err, tex) => {
            if (err) return
            this.avatar.spriteFrame = new cc.SpriteFrame(tex);
        });
    }

    reset() {
        this.marker.active = false;
        // this.fold.active = false;
        this.cards = [];
        this.isSubUser = false
        this.inRound = true;
        this.prepareCards = new CardGroup([], CardGroup.Ungrouped);
        this.timer.hide();
        this.numOfAce = 0;
        this.point = 0;
        this.bonusType = 0;
        this.moveUserPosition(true);
        this.showHideActionUser();
        this.isBonusAce = false;
        this.isSplit = 0

        if (this.subUser) {
            this.subUser.reset()
            this.subUser = null
        }
    }

    resetWinLoseEffect() {
        try {
            this.winEffect.node.active = false;
            this.loseEffect.node.active = false;
            this.blackjack.node.active = false;
            this.prevAction = '';
            this.showHideActionUser();
        } catch (error) {
        }
    }

    setBetLabel(betNum: number) {
        try {
            this.betLbl.string = util.numberFormat(betNum);
        } catch (error) {

        }
    }

    split = (subUser: Player) => {

        this.subUser = subUser
        this.subUser.isSubUser = true
        this.blinkShowHideActionUser('split');
        this.subUser.blinkShowHideActionUser('split');
        if (this.isSplit) return
        this.isSplit = 1
        this.moveUserPosition();
        if (this.cards[1].rank === 1) this.numOfAce--
        this.cards.map((card, index) => {
            if (index === 1) {
                return this.subUser.push(card, 0)
            }
            card.node.zIndex = index === 1 ? -1 : index
            let pos = this.getCardRootPosition();
            pos.x += index < 2 ? 0 : index * this.cardMargin;
            let move = cc.sequence(
                cc.delayTime(0),
                cc.moveTo(0.2, pos));
            card.node.runAction(move);
        })
        setTimeout(() => {
            this.cards = this.cards.filter((card, index) => index !== 1)
        }, 0);

        this.updateCardCounter()
        let point = this.getPoint()
        this.updatePoint(point)
    }

    moveUserPosition(isReset = false) {
        if (!this.isSplit) return

        this.node.x += (isReset ? -1 : 1) * 170;
        this.node.y += (isReset ? -1 : 1) * 10;
        this.cardRootPosition = null;
    }

    push(card: Card, delay: number) {

        // console.log('====================================');
        // console.log(card.rank, card.suit, card.name);
        // console.log('====================================');
        if (!this.cardMargin) this.cardMargin = 0.5 * Player.CARD_MARGIN
        if (!this.inRound && this.subUser) return this.subUser.push(card, delay)

        card.node.zIndex = this.cards.length;
        this.cards.push(card);
        if (card.rank === 1) this.numOfAce += 1

        let point = this.getPoint()

        let fun = () => {
            if (point == 21 && this.cards.length === 2) {
                !this.isCai() && this.showHideActionUser(true, "black jack");
            } else {
                this.updatePoint(point);
            }
            if (point > 20) this.inRound = false
            this.cards[0]?.show();
            if (this.isCai() && this.cards.length > 2) {
                this.cards[1]?.show();
                card?.show()
            }

            if (this.isUser()) {
                card?.show();
                this.reOrderCard(delay)
            }
            else {
                this.updateCardCounter()
            }
        }

        if (this.isBot() && !this.isCai() && this.cards.length === 2) {
            fun = () => {
                this.cards[1]?.show();
                this.updatePoint();
                if (point == 21 && this.cards.length === 2) {
                    this.setInRound(false);
                    this.showHideActionUser(true, "black jack");
                }
            }
        }


        // this.updatePoint()
        let pos = this.getCardRootPosition();


        let scale = cc.sequence(cc.delayTime(delay),
            cc.scaleTo(0.2,
                this.isUser() ? .7 : 0.55));
        card.node.runAction(scale);
        // if (this.isBot()) {

        //     if (this.cards.length < 3) {
        //         pos.x += (this.cards.length - 1) * this.cardMargin;
        //     } else {
        //         pos.x += 2 * this.cardMargin;
        //     }


        // } else {
        pos.x += (this.cards.length - 1) * this.cardMargin * (this.isUser() ? 1.7 : 1.3);
        // }

        let move = cc.sequence(cc.delayTime(delay), cc.moveTo(0.2, pos),
            cc.callFunc(fun),
            cc.callFunc(() => {
                if (this.isBot() && !this.isCai()) {
                    card.show()
                }
            }));
        card.node.runAction(move);
        Media.playAudio('chiabai');
    }

    reOrderCard(delay) {
        if (this.cards.length < 3) return
        let posRoot = this.getCardRootPosition();
        posRoot.x -= (this.cards.length - 1) * this.cardMargin / 2
        this.cards.map((card, index) => {
            let pos = posRoot.clone();
            pos.x += index * this.cardMargin * (this.isUser() ? 1.7 : 1);

            let move = cc.sequence(cc.delayTime(delay), cc.moveTo(0.2, pos));
            card.node.runAction(move);

        })
    }

    showCard2 = () => {
        // if (this.isBot() && !this.isCai() && this.cards.length === 2) {
        //     console.log('====================================');
        //     console.log(123);
        //     console.log('====================================');
        //     this.cards[1].show()
        // }
    }

    touch(pos: cc.Vec2): Card {
        for (let i = this.cards.length - 1; i >= 0; --i) {
            let card = this.cards[i];
            if (card.node.getBoundingBoxToWorld().contains(pos)) {
                return card;
            }
        }
        return null;
    }

    selectCard(card: Card) {
        // this.prepareCards.push(card);
        // this.prepareCards.calculate();
        // card.setPositionY(this.marker.position.y + this.node.position.y + 30);
    }

    unselectCard(card: Card) {
        // this.prepareCards.remove(card);
        // this.prepareCards.calculate();
        // card.setPositionY(this.marker.position.y + this.node.position.y);
    }

    removeCards(cards: CardGroup) {
        for (let i = cards.count() - 1; i >= 0; --i) {
            helper.removeBy(this.cards, cards.at(i));
        }

        this.prepareCards = new CardGroup([], CardGroup.Ungrouped);
    }

    setActive(on: boolean) {
        if (on) {
            this.timer?.show(Player.TIME);
        } else {
            this.timer.hide();
        }
    }

    setTimeCallback(selector: Function, selectorTarget: any, data: any) {
        this.timer.onCompleted(selector, selectorTarget, data);
    }

    showHandCards() {
        for (let i = 0; i < this.cards.length; i++) {
            this.cards[i]?.show();
        }
    }

    reorder() {
        for (let i = this.cards.length - 1; i >= 0; --i) {
            let pos = this.getCardRootPosition();
            pos.x += i * this.cardMargin * this.cardDirection;
            let card = this.cards[i];
            card.node.setPosition(pos);
            card.node.zIndex = this.seat == 1 ? this.cards.length - i : i;
        }
    }

    sortHandCards() {
        helper.sort(this.cards);
        this.reorder();
    }

    showCardCounter() {
        this.marker.active = false//true;
        this.updateCardCounter();
    }

    hideCardCounter() {
        this.marker.active = false;
    }

    updateCardCounter() {
        try {
            this.labelCardCounter.node.active = false
            this.labelCardCounter.string = this.cards.length.toString();
        } catch (error) {

        }
    }

    updatePoint(label?: number | string) {
        // if (Number(this.pointLbl.string) === NaN) return
        this.point = this.getPoint();

        let showPoint: any = this.point;

        if (this.inRound && this.numOfAce && this.isBonusAce && this.point < 21) {
            showPoint = (showPoint - 10) + '/' + showPoint
        }

        try {
            this.pointLbl.string = showPoint
        } catch (error) {

        }
    }

    getPoint = () => {

        // if (this.cards.length === 2) {
        //     this.isBonusAce = true;
        //     this.point = 21;
        //     return 21
        // }


        // fake dealer BlackJack
        // if (this.isCai() && this.cards.length == 2) {
        //     return 21
        // }
        var point = this.cards.reduce((point, current) => {
            if (current.rank === 1) {
                return point
            } else if (current.rank > 10) {
                return point + 10
            } else {
                return point + current.rank
            }
        }, 0);

        this.isBonusAce = point < 12

        if (this.numOfAce > 0) {
            point += this.numOfAce * 1
                + (this.isBonusAce ? 10 : 0)
        }
        this.point = point
        return point;
    }

    checkBlackJack = () => {
        let rank1 = this.cards[0]?.rank
        let rank2 = this.cards[1]?.rank

        if (rank1 === 1 || rank2 === 1) {
            if (rank1 > 9 || rank2 > 9) {
                this.setInRound(false)
                this.updatePoint(21)
                !this.isCai() && this.showHideActionUser(true, 'black jack')
                // this.updatePoint("Black Jack!")
                return true
            }
        }
        return false
    }

    private getCardRootPosition() {
        if (!this.cardRootPosition) {
            this.cardRootPosition = cc.v2(this.marker.position.x * this.node.scaleX + this.node.position.x,
                this.marker.position.y * this.node.scaleY + this.node.position.y +
                (this.isUser() ? 0 : 10)
            );
        }
        return cc.v2(this.cardRootPosition);
    }

    static readonly CARD_MARGIN: number = 30;
    static readonly TIME: number = 30;
}
