import Api from "./Api";
import Config from "./Config";
import util from "./util";

const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Label)
    coin: cc.Label = null;

    @property(cc.Label)
    userName: cc.Label = null;

    @property(cc.Sprite)
    avatar: cc.Sprite = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        cc.systemEvent.on('updateProfile', this.updateProfile, this)
        this.updateProfile()
    }

    updateProfile() {
        try {
            this.coin.string = util.numberFormat(Api.coin);
            this.userName.string = Api.username;
            cc.assetManager.loadRemote<cc.Texture2D>(Api.photo, (err, tex) => {
                if (err) return
                this.avatar.spriteFrame = new cc.SpriteFrame(tex);
                Config.userphoto = tex;
            });
        } catch (error) {
            console.log('====================================');
            console.log('update profile error', error);
            console.log('====================================');
        }
    }

    onDestroy() {
        try {
            cc.systemEvent.clear()
        } catch (error) {

        }
    }

    // update (dt) {}
}
