const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    @property(cc.Sprite)
    effect: cc.Sprite = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        let bound = this.node.width / 2;
        cc.tween(this.effect.node).repeatForever(
            cc.tween()
                .to(2, { position: cc.v2(this.node.x + bound + 20, this.node.y) })
                .delay(2)
                .to(2, { position: cc.v2(this.node.x - bound, this.node.y) })
                .delay(2)
        ).start();
    }

    toggle() {
        this.node.active = !this.node.active;
    }

    update(dt) {

    }
}
