import LBEntry from "./LBEntry";
import Media from "./Media";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Leaderboard extends cc.Component {
    @property(cc.Node)
    template: cc.Node = null;

    @property(cc.Node)
    viewFriend: cc.Node = null;

    @property(cc.Node)
    viewGlobal: cc.Node = null;

    @property(cc.Button)
    friendTabBtn: cc.Button = null;

    @property(cc.Button)
    globalTabBtn: cc.Button = null;

    @property(cc.Node)
    containerFriend: cc.Node = null;


    @property(cc.Node)
    containerGlobal: cc.Node = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    toggleTab(sender, arg) {
        Media.playAudio('click');
        let friendActive = arg == 'friend';
        this.viewFriend.active = friendActive;
        this.friendTabBtn.interactable = !friendActive;
        this.viewGlobal.active = !friendActive;
        this.globalTabBtn.interactable = friendActive;
        let getContentScrollView = (node) => {
            return node.getChildByName('content');
        }
        this.node.getComponent(cc.ScrollView).content = friendActive ? getContentScrollView(this.viewFriend) : getContentScrollView(this.viewGlobal);
        this.setColor(this.friendTabBtn.node, friendActive);
        this.setColor(this.globalTabBtn.node, !friendActive);
    }

    setColor(target: cc.Node, isActive: boolean) {
        let color = isActive ? cc.Color.WHITE : cc.Color.GRAY;
        target.color = color
    }

    start() {
        this.setColor(this.globalTabBtn.node, false);
    }

    // update (dt) {}

    render(rank: number, name: string, coin: number, avatar: string, playerId: string, type: string) {
        let entry = cc.instantiate(this.template);
        entry.getComponent(LBEntry).render(rank, name, coin, avatar, playerId, type);
        if (type == 'friend') this.containerFriend.addChild(entry);
        else this.containerGlobal.addChild(entry);
        entry.active = true;
        // this.template.active = false
    }

    onLoadComplete() {
        //     this.template.active = false
        //     console.log('====================================');
        //     console.log('load complete');
        //     console.log('====================================');

    }

}
