export const UItype = {
    noAd: "noAd",
    confirmViewAd: "confirmViewAd",
    dailybonus: "dailybonus"
}

export default class UI {
    private static _contentViewAd = ''

    public static get contentViewAd() {
        return UI._contentViewAd;
    }

    public static set contentViewAd(key: string) {
        UI._contentViewAd = key;
        UI.showConfirmViewAd();
    }

    public static showAdNotLoad() {
        cc.systemEvent.emit(UItype.noAd)
    }

    public static showConfirmViewAd() {
        cc.systemEvent.emit(UItype.confirmViewAd)
    }

    public static showDailybonus() {
        cc.systemEvent.emit(UItype.dailybonus)
    }
}