import Config from "./Config"

const customHelper = {
    loadBannerAd: function () {
        let sp = FBInstant.getSupportedAPIs();

        console.log('====================================');
        console.log(sp);
        console.log('====================================');

        FBInstant.loadBannerAdAsync(Config.bannerAdID).then(rs => {
            console.log('====================================');
            console.log(rs);
            console.log('====================================');
        }).catch(err => {
            console.log('====================================');
            console.log('err banner ad', err);
            console.log('====================================');
        })
    },
    hideBannerAd: function () {
        FBInstant.hideBannerAdAsync();
    }
}

export default customHelper;