const { ccclass, property } = cc._decorator;

@ccclass
export default class DailyBonusItem extends cc.Component {

    @property(cc.Label)
    value: cc.Label = null;

    @property(cc.Node)
    claimed: cc.Node = null;

    // @property(cc.Node)
    // extra: cc.Node = null;

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        
    }

    // update (dt) {}

    setClaimable(claimable: boolean, isClaimed: boolean) {
        this.claimed.active = isClaimed;
        this.getComponent(cc.Button).interactable = claimable;
        this.getComponentInChildren(cc.Button).interactable = claimable;
        let color = isClaimed ? cc.Color.GRAY : cc.Color.WHITE;
        this.node.children[4].color = color;
        this.node.children[4].children.map(el => {
            el.color = color;
        })
        // this.extra.active = claimable;
    }

}
