const tutor = `Цель игры - собрать комбинацию карт больше, чем у дилера, но не более 21 очка.‌
 ‌
<size=30><b>Blackjack</b></size> 
Если первые две карты в руке дают в сумме 21 очко, эта комбинация называется «Blackjack».
$img
Если у крупье есть Blackjack, проигрывают все игроки, кроме тех, у кого есть Blackjack.. Если и у дилера, и у игрока есть blackjack, это считается ничьей, и игрок получает обратно свою ставку.
Если у игрока есть blackjack, а у дилера нет, игрок выигрывает и получает выплату 3: 2 по своей ставке.
Если открытая карта дилера стоит 10 очков, он смотрит на свою закрытую карту. Если у дилера есть blackjack, они переворачивают свои карты, и раунд заканчивается.

<size=30><b>Действия игрока.:</b></size> 

Если ни у игрока, ни у дилера нет blackjack, игрок может выбрать одно из нескольких действий после раздачи карт.
«Split» - это действие доступно только в том случае, если в руке игрока есть две карты одного достоинства. Игрок удваивает свою ставку, делит руку пополам и берет две дополнительные карты. Затем обе руки разыгрываются независимо.
$img
«Hit» - игрок берет еще одну карту. Это действие можно повторять до тех пор, пока ценность руки не превысит 21 балл.
$img
«Double» - игрок удваивает свою ставку, берет еще одну карту и остается.
$img
«Stand» - игрок больше не берет карты и сохраняет свою текущую руку.
$img
<size=30><b>Дилер</b></size> 
Когда все игроки встали, дилер переворачивает их закрытую карту.
При необходимости дилер может брать дополнительные карты, пока они не наберут 17 и более очков. Если счет дилера превышает 21, все игроки, которые не превысили 21, автоматически выигрывают независимо от их счета.

<size=30><b>Страхование</b></size> 

Особый случай: если у дилера открытый туз, игроки могут сделать дополнительную «страховочную» ставку, равную половине их первоначальной ставки. После того, как все игроки приняли решение, дилер смотрит их закрытую карту. Если у дилера blackjack, по всем «страховым» ставкам выплачивается выплата 2: 1. Если у дилера нет blackjack, все страховые ставки идут в банк, и игра продолжается как обычно.
$img
<size=30><b>Payout</b></size> 

Ставка: 1: 1
Blackjack: 3:2
Ничья: ставка возвращается.
Бюст: ставка проиграна
Страхование: 2: 1
<size=30><b>Подсчет очков:</b></size> 

При подсчете очков используются следующие значения карт: карты от 2 до 10 приносят количество очков, равное номинальной стоимости карты.
$img
Карты J, Q и K приносят 10 очков.
$img
Туз приносит 1 или 11 очков в зависимости от того, какое количество очков представляет руку, превышающую 21.
$img`

