import Api from "./Api";
import Config from "./Config";
import Helper from "./Helper";
import Media from "./Media";
import util from "./util";

const { ccclass, property } = cc._decorator;

@ccclass
export default class Popup extends cc.Component {
    @property(cc.Node)
    popups: cc.Node[] = [];

    @property(cc.Label)
    addCoin: cc.Label = null;

    @property(cc.Label)
    betLabel: cc.Label = null;

    // LIFE-CYCLE CALLBACKS:

    onLoad() {

    }

    start() {
        Popup.instance = this;
    }

    // update (dt) {

    // }

    changeOpacityBg(opacity = 255) {
        let bg = this.node.children[0];
        bg.opacity = opacity
    }

    closePopup() {
        Media.playAudio('click');
        this.node.active = false;
        this.popups.forEach(p => {
            p.active = false;
        });
    }

    close(sender: cc.Node, id: number) {
        Media.playAudio('click');
        this.changeOpacityBg()
        this.node.active = false;
        this.popups.forEach(p => {
            p.active = false;
        })
    }

    open(sender: cc.Node, id: number, opacity = 255, data?: any) {
        this.changeOpacityBg(opacity)
        this.node.active = true;
        if (id) {
            this.popups[id - 1].active = true;
        }
        try {
            let betting_lbl = cc.find('Canvas/popup/bet/betting_value/Title')
            betting_lbl.getComponent(cc.Label).string = Helper.renderDot(Config.betValue)
        } catch (error) {

        }

        if (id == 3) {
            Media.playAudio('click');
        }

        if (id == 8) {
            let popup7 = this.popups[7]
            popup7.getComponentInChildren(cc.Label).string = data.text;

            let isCoin = data.type == 'coin'
            popup7.children[1].children[0].active = isCoin;
            popup7.children[1].children[1].active = !isCoin;
        }
    }

    onChangeBetValue(object, value) {

        // let betting_lbl = cc.find('Canvas/popup/bet/betting_value/Title');
        let betValue = value === '0' ? 3000 : (Config.betValue + +value);
        if (betValue == Config.betValue) return;
        Media.playAudio('changeBet');
        if (betValue > Api.coin) betValue = Api.coin;

        this.addCoin.node.stopAllActions();
        this.addCoin.node.opacity = 0;
        let changeValue = value;
        value == '0' && (changeValue = 3000 - Config.betValue)

        if (Config.betValue == betValue) return;

        Config.betValue = betValue;

        this.betLabel.string = Helper.renderDot(betValue)
        this.addCoin.string = (changeValue < 0 ? '-' : '+') + util.numberFormat(Math.abs(changeValue))

        let action = [
            cc.moveTo(0, cc.v2(this.betLabel.node.x, this.betLabel.node.y + 20)),
            cc.moveTo(1, cc.v2(this.betLabel.node.x, this.betLabel.node.y + 120)),
        ]

        this.addCoin.node.opacity = 255
        cc.tween(this.addCoin.node)
            .to(1.5, { opacity: 0 })
            .start()

        this.addCoin.node.runAction(
            cc.sequence(action)
        )
    }

    closeSelectLangPopup(event) {
        event.target.parent.parent.active = false
        // cc.systemEvent.emit('firstTut');
        // Api.saveUserData({ startAt: Date.now() }).then(function () {
        //     cc.log('data is set');
        // });
        console.log('====================================');
        console.log(event);
        console.log('====================================');
    }

    static openChangeLang() {
        let changeLangPopup = cc.find('Canvas/SelectLanguage');
        changeLangPopup.active = true
    }

    public static instance: Popup = null;
}
