var Roller = require('slot.Roller');
import Api from './Api';
import Config from './Config'
import EventKeys from './EventKeys';
import Language from './Language';
import Media from './Media';
const { ccclass, property } = cc._decorator;
import Modal from "./popop/Modal";
import UI from './UI';
let initBienSlotMachine = {
    8: .05,
    7: .05,
    6: .05,
    5: .15,
    3: 0.2,
    2: .5,
}
let listOfKey = Object.keys(initBienSlotMachine).reverse();

@ccclass
export default class SlotMachine extends cc.Component {
    @property({ type: cc.AudioClip })
    audioSpinRun: cc.AudioClip = null;
    @property({ type: cc.AudioClip })
    audioSpinFail: cc.AudioClip = null;
    @property({ type: cc.AudioClip })
    audioSpinOK: cc.AudioClip = null;
    @property({ type: cc.Node })
    spinBtn: cc.Node = null;
    @property({ type: cc.Node })
    spinBtnAd: cc.Node = null;
    @property({ type: cc.Label })
    lbNumOfSpin: cc.Label = null;
    @property(Modal)
    modal: Modal = null;
    spining: boolean = false;
    rollers = [];
    @property(cc.Node)
    fly_chip: cc.Node = null
    bienSlotMachine = {};
    tempBienSlotMachine = initBienSlotMachine

    @property(cc.Node)
    cangat: cc.Node = null
    // start() {

    // }

    onLoad() {
        this.rollers = this.node.getComponentsInChildren(Roller);
        this.resetValue()
    }

    resetValue() {
        this.spining = false
        setTimeout(() => {
            cc.systemEvent.emit('resetSlotSymbolIndex')
        }, 0);
    }

    showHideSpin(needShow) {
        this.resetValue();
        this.toggleCanGat();
        this.toggleBtnSpin()
        this.node.parent.active = needShow
        this.node.active = needShow;
        this.fly_chip.active = false;
    }

    toggleBtnSpin() {
        this.lbNumOfSpin.string = Api.ticket.toString();
        let showBtnAd = !Api.ticket;
        this.spinBtn.active = !showBtnAd;
        this.spinBtnAd.active = showBtnAd;
    }

    toggleCanGat() {
        this.cangat.children[1].active = !this.spining;
        this.cangat.children[2].active = !this.spining;
        this.cangat.children[3].active = this.spining;
        this.cangat.children[4].active = this.spining;

    }

    spin() {
        if (this.spining) return
        Media.playAudio('slotting')
        let rs = this.makeRandomResult()
        Api.ticket--;
        Api.updateTicket();
        this.spining = true
        // var audioID = cc.audioEngine.playEffect(this.audioSpinRun, false);
        this.toggleCanGat()
        this.rollers.map((roller, index) => {
            roller.toggleSpin();
        })
        setTimeout(() => {
            this.rollers.map((roller, index) => {
                let resultIndex = Config.slotMachineEnum[rs[index]]
                roller.toggleSpin(resultIndex);
            })
            // cc.audioEngine.stopEffect(audioID);
            let bonusType = this.calulateResult(rs)
            // cc.audioEngine.playEffect(bonusType != 1 ? this.audioSpinOK : this.audioSpinFail, false);
            setTimeout(() => {
                this.showHideSpin(false)
            }, 2000);
        }, 3500);
    }

    spinAd() {
        UI.contentViewAd = 'endgameConfirm' 
        Api.showRewardedVideo(() => {
            Api.ticket++;
            this.spin();
        }, () => {
            this.showHideSpin(false);
            UI.showAdNotLoad()
            cc.systemEvent.emit('SlotMachineFail', 200000);
            // Api.logEvent(EventKeys.POPUP_ADREWARD_SPIN_ERROR);
            // this.modal.show(Language.getInstance().get('NOVIDEO'));
        })
    }

    makeRandomResult() {
        let rs = [];
        // reset logic
        this.changeSlotLogic(null);

        // get slot 1 
        rs.push(this.slotMachineLogic());
        this.changeSlotLogic(rs[0]);
        // get slot 2
        rs.push(this.slotMachineLogic());
        this.changeSlotLogic(rs[1]);
        // get slot 3
        rs.push(this.slotMachineLogic());
        this.bienSlotMachine = initBienSlotMachine
        return rs;
    }

    changeSlotLogic(prevKey) {
        let lastMoc = 0;
        listOfKey.map((key, index) => {
            if (prevKey) {
                this.tempBienSlotMachine[key] += (key == prevKey ? 0.1 : -.02)
            } else {
                this.tempBienSlotMachine = initBienSlotMachine;
            }
            this.bienSlotMachine[key] = lastMoc += this.tempBienSlotMachine[key]
        })
    }

    slotMachineLogic() {
        let random = Math.random();
        for (let key of listOfKey) {
            if (random <= this.bienSlotMachine[key]) return key
        }
    }

    calulateResult(restult = []) {
        let bonus = 1
        if (restult[0] === restult[1] && restult[0] === restult[2]) {
            bonus *= restult[0] === 7 ? 10 : restult[0]
        } else if (restult[0] == restult[1] || restult[0] == restult[2] || restult[2] == restult[1]) {
            bonus = 1.5
        }

        if (bonus != 1) {
            cc.systemEvent.emit('SlotMachineBonus', bonus)
            this.fly_chip.active = true;
            Media.playAudio('slotWin')
        }
        return bonus
    }

    hideSlotMachine() {
        this.node.parent.active = false;
        this.node.active = false;
        this.fly_chip.active = false;
    }
}
