import Api from "./Api";
import util from "./util";

const { ccclass, property } = cc._decorator;

@ccclass
export default class SelectLanguage extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property
    text: string = 'hello';

    pageView: cc.PageView = null
    currentIndex: number = 0;
    maxIndex: number = 2
    lang = ['en', 'ru']
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {
        this.pageView = this.node.getComponent(cc.PageView);

        if (/ru/.test(Api.locale)) {
            this.onChangeLangBtnPress(null, 'next')
        }

        // console.log('====================================');
        // console.log(this);
        // console.log('====================================');
        // console.log('====================================');
        // console.log(this.pageView);
        // console.log('====================================');
        // console.log('====================================');
        // console.log('select langguage', slider);
        // console.log('====================================');
    }

    onPaging(event: object, data: any) {
        const index = this.pageView.getCurrentPageIndex();

        const newLang = this.lang[index]
        this.currentIndex = index;
        // Api.locale = newLang;

        setTimeout(() => {
            util.changeLange(newLang)
        }, 80);
    }

    onChangeLangBtnPress(event, type: string) {
        let newIndex = this.currentIndex + (type == 'next' ? 1 : -1)
        newIndex = newIndex > 0 ? newIndex : 0
        newIndex = newIndex > this.maxIndex ? this.maxIndex : newIndex
        this.pageView.scrollToPage(newIndex)


        // var langePageView = cc.find('Canvas/SelectLanguage/langePageview')
        // console.log('====================================');
        // console.log(langePageView);
        // console.log('====================================');
        // console.log('====================================');
        // console.log(type);
        // console.log('====================================');
    }

    hideChangeLanguage() {
        var langePageView = cc.find('Canvas/SelectLanguage/langePageview')
        // console.log('====================================');
        // console.log();
        // console.log('====================================');
    }

    // update (dt) {}
}
