import Api from "./Api";
import Config from "./Config";
import Language from "./Language";

export default class util {
    static readonly prefix = ["", "k", "M", "G", "T", "P", "E", "Z", "Y", "x10^27", "x10^30", "x10^33"]; // should be enough. Number.MAX_VALUE is about 10^308

    // public static assetBtn = null;

    // public static getEffectActionResouces() {
    //     cc.resources.load('btns', cc.SpriteAtlas, (err, atlas) => {
    //         util.assetBtn = atlas;
    //     });
    // }

    public static assetCard = null;

    public static preloadAssetCard() {
        if (util.assetCard) return;
        cc.resources.load('cards', cc.SpriteAtlas, (err, atlas) => {
            util.assetCard = atlas;
        });
        // return util._assetCard
    }

    public static profileBot = null;

    public static preloadBotAsset() {
        if (util.profileBot) return;
        cc.resources.load('profileBot', cc.SpriteAtlas, (err, atlas) => {
            util.profileBot = atlas;
        });
    }

    public static numberFormat(n: number): string {
        let ii = 0;
        while ((n = n / 1000) >= 1) { ii++; }
        return (Math.round(n * 10 * 1000) / 10) + util.prefix[ii];
    }

    public static playAudio(audioClip: cc.AudioClip) {
        // if (Config.soundEnable)
        //     cc.audioEngine.playEffect(audioClip, false);
    }

    public static logTimeInGame = (time: number) => {
        if ([30, 60, 90, 120, 180, 270, 510].indexOf(time) > -1 || time === 0) {
            Api.logEvent(`${time}s_sstime`);
        }
    }

    public static updateProfile() {
        cc.systemEvent.emit('updateProfile')
    }

    public static changeLange(newLang: string) {
        if (Api.locale == newLang) return
        Api.locale = newLang
        Language.getInstance().load(newLang);
        Api.saveUserData({
            lang: newLang
        })
        cc.systemEvent.emit('LANG_CHAN');
    }

    public static checkIsGamePlatform() {
        return util.isFBinstant() || util.isYandexGame()
    }

    public static isFBinstant() {
        return window.hasOwnProperty('FBInstant')
    }

    public static isYandexGame() {
        return window.hasOwnProperty('ysdk')
    }
}
