    calculateShowActionUser = () => {
        let actions = [];
        const canDouble = this.myself.point <= 10 && this.myself.coinVal >= 2 * Config.betValue;
        const canInsurr = !this.myself.isSplit && this.players[0].cards[0].rank === 1;
        const canSplit = this.myself.cards.length === 2 && !this.myself.isSplit && this.myself.cards[0].rank === this.myself.cards[1].rank // neu co 2 cay giong nhau

        if (canSplit) {
            actions = [
                { ...ACTIONS_ALL.split, clickable: true },
                { ...ACTIONS_ALL.hit, clickable: true },
                { ...ACTIONS_ALL.stand, clickable: true },
                !canInsurr && canDouble ?
                    { ...ACTIONS_ALL.double, clickable: canDouble } :
                    { ...ACTIONS_ALL.insurr, clickable: canInsurr },
            ];
        } else {
            actions = [].concat(init_actions);
            actions[0].clickable = true;
            actions[1].clickable = true;
            actions[2].clickable = canInsurr;
            actions[3].clickable = canDouble;
        }

        this.applyButton(actions);
    }

    applyButton(btns = init_actions) {
        btns.map((btn, index) => {
            let _btn = this.actionButotns[index]
            _btn.getComponentInChildren(cc.Label).string = Language.getInstance().get(btn.name);//btn.name.toUpperCase();
            _btn.clickEvents[0].handler = btn.handle;
            this.toogleBtn(index, btn.clickable);
        })
    }

    toogleBtn = (idBtn, isToogle) => {
        this.actionButotns[idBtn].interactable = isToogle
        let btnColor = isToogle ? cc.Color.WHITE : cc.color(48, 46, 56, 255)
        let btnTitle = isToogle ? cc.Color.WHITE : cc.color(70, 70, 70, 255)
        this.actionButotns[idBtn].getComponentInChildren(cc.Sprite).node.color = btnColor;
        this.actionButotns[idBtn].getComponentInChildren(cc.Label).node.color = btnTitle;
    }

    disableToggleActionUser = () => {
        init_actions.map(btn => {
            btn.clickable = false
        })
        this.actionButotns.map((btn, i) => {
            this.toogleBtn(i, false)
        })
    }

    onStandClicked(isPressed = true) {
        Media.playAudio('click');
        let player = this.myself
        player.setActive(false);
        isPressed && player.showHideActionUser(true, 'stand')
        if (!player.inRound && player.subUser) {
            player = player.subUser
        }
        this.disableToggleActionUser()
        player.setInRound(false);
        this.nextTurn()
    }

    onHitClicked(isPressed = true) {
        Media.playAudio('click');
        let player = this.myself
        this.takeCard(player)
        player.setActive(true);
        this.disableToggleActionUser();
        isPressed && this.myself.blinkShowHideActionUser('hit');
    }

    onDoubleClicked() {
        Media.playAudio('click');
        this.myself.showHideActionUser(true, 'double');
        this.myself.setBonusType(1);
        this.onHitClicked(false);
        this.disableToggleActionUser();
    }


    onInsurrClicked() {
        Media.playAudio('click');
        this.myself.setBonusType(2);
        this.onStandClicked();
        this.myself.showHideActionUser(true, 'insurr');
        this.disableToggleActionUser();
    }

    async onSplitClicked() {
        Media.playAudio('click');
        this.disableToggleActionUser()
        this.myself.split(this.seats[6]);
        this.seats[6].show();
        await Game.sleep(Game.DEAL_SPEED);
        this.takeCard(this.myself, false)
        await Game.sleep(500);
        this.takeCard(this.myself.subUser, false)
        await Game.sleep(1000);
        this.nextTurn();
    }

    static sleep = async (wait: number) => {
        return new Promise(resolve => {
            setTimeout(() => {
                resolve(1);
            }, wait);
        })
    }

    toogleSlotMachine() {
        this.popup.getComponentInChildren(SlotMachine).showHideSpin(true)
    }

    onSlotMachineBonus(bonus) {
        let bonusCoin = bonus * Config.betValue;
        try {
            this.myself.changeCoin(bonusCoin);
            Api.updateCoin(this.myself.coinVal);
        } catch (error) {

        }

    }

    onSlotMachineBonusFail(bonus = 200000) {
        try {
            this.myself.changeCoin(bonus);
            Api.updateCoin(this.myself.coinVal);
        } catch (error) {

        }
    }

    static readonly WAIT: number = 0; // wait for player
    static readonly DEAL: number = 1; // deal card
    static readonly PLAY: number = 2; // play
    static readonly LATE: number = 3; // late
