    static readonly DEAL_SPEED: number = 1.2//1.66;
    static readonly CARD_SPACE: number = 45;
    static readonly WAIT_BOT: number = 2000;
    static readonly WAIT_RE_SHOW_USER_ACTION: number = 1000;

