import util from "./util";
import Api from "./Api";
import Media from "./Media";

const { ccclass, property } = cc._decorator;

@ccclass
export default class LBEntry extends cc.Component {
    @property(cc.Label)
    rank: cc.Label = null;
  
    @property(cc.Sprite)
    avatar: cc.Sprite = null;
    @property(cc.Label)
    playerName: cc.Label = null;
    @property(cc.Label)
    playerCoin: cc.Label = null;
    @property(cc.SpriteFrame)
    spriteMedals: cc.SpriteFrame[] = [];

    private playerId: string = null;
    private photo: cc.Texture2D = null;
    private coin: number = 0;
    private click_key: string = 'lb_battle';
    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    render(rank: number, name: string, coin: number, avatar: string, playerId: string, type: string) {
        this.playerId = playerId;
        this.playerName.string = name;
        this.playerCoin.string = util.numberFormat(coin);
        this.coin = coin;

        let btnPlayWith = this.getComponentInChildren(cc.Button)
        if(type === 'global') btnPlayWith.node.active = false
        cc.assetManager.loadRemote<cc.Texture2D>(avatar, (err, tex) => {
            if (err) return
            this.photo = tex;
            this.avatar.spriteFrame = new cc.SpriteFrame(tex);
        });

        this.rank.string = '' + (rank + 1);
        // setTimeout(() => {
        //     this.node.children[0].x -= rank * 30 * .75
        // }, 300);

        // this.getComponent(cc.Sprite).node.y += 10
        // this.node.children[0].x += rank * 30
        // if (this.spriteMedals.length >= rank) {
        //     this.rank.node.active = false;
        //     this.medal.spriteFrame = this.spriteMedals[rank-1];
        // } else {
        //     this.rank.string = util.numberFormat(rank);
        //     this.medal.node.active = false;
        // }

        if (Api.playerId == playerId) {
            this.click_key = 'lb_share';
        }
    }

    onClick() {
        Media.playAudio('click');
        // console.log('====================================');
        // console.log(this.click_key, this.playerId, this.photo, this.playerName.string, this.coin);
        // console.log('====================================');
        // return
        cc.systemEvent.emit(this.click_key, this.playerId, this.photo, this.playerName.string, this.coin);
    }
}
