import Api from "./Scripts/Api";
import Popup from "./Scripts/Popup";
import Config from "./Scripts/Config";
import Media from "./Scripts/Media";
import UI from "./Scripts/UI";

const { ccclass, property } = cc._decorator;

@ccclass
export default class FirstTut extends cc.Component {

    @property(Popup)
    popup: Popup = null;

    @property(cc.Node)
    hand: cc.Node = null

    @property(cc.Node)
    target: cc.Node = null

    blinkAction = null;
    scrollAction = null;

    @property(cc.Node)
    guide: cc.Node = null

    @property(cc.Button)
    btnGotit: cc.Node = null

    @property(cc.Node)
    mask: cc.Node = null

    @property(cc.Node)
    btnPlay: cc.Node = null

    // LIFE-CYCLE CALLBACKS:

    onLoad() {
        this.blinkAction = cc.blink(5000, 10000);
        let scroll = this.guide.getComponentInChildren(cc.ScrollView);

        scroll.node.on('touch-up', () => {
            if (!this.hand.active) return;
            this.hand.active = false
        })

        cc.tween(this.btnPlay).repeatForever(
            cc.tween()
                .to(.5, { scale: 1.05 })
                .to(.5, { scale: .95 })
            // .delay(.5)
        ).start();
        // scroll.node.on('scroll-to-bottom', () => {
        //     if (this.btnPlay.node.active) return
        //     this.btnPlay.node.active = true
        // })
    }


    scrollHand() {
        this.hand.active = true;
        cc.tween(this.hand).repeatForever(
            cc.tween()
                .to(0, { position: cc.v2(this.target.x, -150) })
                .to(1.5, { position: cc.v2(this.target.x, 150) })
                .delay(.5)
        ).start();
    }

    openFirstTut() {
        this.guide.active = true;
        this.scrollHand();
    }

    blinkHand() {
        this.hand.active = true;
        this.hand.x = this.target.x;
        this.hand.y = this.target.y;
        let action = cc.sequence([
            // cc.moveTo(0, 0, 0),
            cc.moveTo(0, cc.v2(this.target.x, this.target.y)),
            this.blinkAction
        ])

        this.hand.runAction(action);
    }

    closeFirstTut() {
        Media.playAudio('click');
        this.guide.active = false;
        this.hand.active = false;
        this.hand.stopAllActions();
        UI.showDailybonus();
    }

    start() {
        this.openFirstTut = this.openFirstTut.bind(this);
        this.hideModalTut = this.hideModalTut.bind(this);
        this.btnPlay.active = false;
        cc.systemEvent.on('firstTut', this.openFirstTut);
        cc.systemEvent.on('hideModalTut', this.hideModalTut);
    }

    hideModalTut() {
        this.mask.active = false;
        this.hand.active = false;
    }

    // update (dt) {}

    gotIt() {
        Media.playAudio('click');
        this.guide.active = false;
        this.hand.stopAllActions();
        this.blinkHand();
        // this.popup.open(null, 7);
        this.mask.active = true;
        this.btnPlay.active = true;
    }

    onPlay() {
        Media.playAudio('click');
        this.hand.active = false;
        this.mask.active = false;
        this.btnPlay.active = false;
        this.hand.stopAllActions();
        this.popup.open(null, 7);
    }

    acceptPlay() {
        this.popup.closePopup();
        //    Api.logEvent(EventKeys.PLAY_NOW);
        if (Api.coin > Config.bankrupt) {
            this.popup.open(this.node, 1);
        } else {
            Api.preloadRewardedVideo();
            Api.showInterstitialAd();
            this.popup.open(this.node, 4);
            // Api.logEvent(EventKeys.POPUP_ADREWARD_COIN_OPEN);
        }
    }

    closePopup() {
        this.popup.closePopup();
        if (!Api.dailyBonusClaimed) UI.showDailybonus();
    }

}
