enum audioName {
    'background',
    'changeBet',
    'chiabai',
    'click',
    'deal_complete',
    'lose',
    'slotting',
    'win',
    'slotWin'
}

import Config from "./Config";

export default class Media {

    private static audio = {};

    static init() {
        if (Object.keys(Media.audio).length > 0) return
        let initAudioPromise = Object.keys(audioName).map(audio => {
            return new Promise(resolve => {
                cc.loader.loadRes(`audio/${audio}.mp3`, cc.AudioClip, (err, rs) => {
                    if (!err) Media.audio[audio] = rs;
                    resolve(1);
                })
            })
        })

        Promise.all(initAudioPromise).then(rs => {
            cc.audioEngine.play(Media.audio['background'], true, .3)
        })
    }

    static pauseAllMedia() {
        cc.audioEngine.pauseAll();
    }

    static resumeAllmedia() {
        cc.audioEngine.resumeAll();
    }

    static toggleSound() {
        Media.playAudio('click');
        setTimeout(() => {
            Config.soundEnable = !Config.soundEnable;
            if (Config.soundEnable) {
                Media.resumeAllmedia();
            } else {
                Media.pauseAllMedia();
            }
        }, 300);
    }

    static playAudio(audio: keyof typeof audioName) {
        Config.soundEnable && cc.audioEngine.play(Media.audio[audio], false, 1)
    }
}